.muted {
    color: rgb(173, 173, 173);
}

.screenHeight {
    min-height: 100vh;
}

.subheader {
    font-weight: bold;
    text-transform: uppercase;
    font-size: 36px;
    margin-bottom: 26px;
}

.container :global p {
    margin: 26px 0;
}

.container {
    font-family: 'Open Sans', sans-serif;
    max-width: 1020px;
    margin: 0 auto;
}

.header {
    width: min-content;
    margin-top: 80px;
    margin-left: 60px;
    margin-bottom: 100px;
}

.title {
    display: inline-block;
    width: fit-content;
    color: black;
    text-transform: uppercase;
    white-space: nowrap;
    font-size: 24px;
    font-weight: bold;
}
.title .muted {
    text-transform: none;
}

.header img {
    max-width: 100%;
    height: auto;
}

.chessBoard {
    display: grid;
    column-gap: 40px;
    grid-template-columns: 1fr 1fr;
}

.columnImages {
    margin-top: 60px;
    display: grid;
    grid-template-columns: 13fr 81fr 57fr;
    gap: 12px;
}
.columnImages img {
    max-width: 100%;
    margin-bottom: 12px;
}

.columnImages img.shrink {
    display: block;
    max-width: 64%;
    margin-left: auto;
}
.columnImages p {
    max-width: calc(100% - 80px);
}

.embedWrapper {
    margin: 140px 0;
}

.panels {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 12px;
}

.panels img {
    max-width: 100%;
}

.carouselWrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 12px;
    margin-top: 40px;
}

.carouselWrapper :global .swiper-container {
    width: calc(100% - 40px);
    margin-left: 0;
}
.carouselWrapper .paginationWrapper {
    position: relative;
    height: 24px;
    width: calc(100% - 40px);
    margin-left: 0;
}
.carouselWrapper .paginationWrapper :global .swiper-pagination {
    width: 100%;
}
.carouselWrapper .paginationWrapper :global .swiper-pagination-bullet {
    margin: 0 12px;

}

.carouselWrapper img {
    max-width: 100%;
}

.carouselWrapper .textWrapper {
    padding-top: 80px;
    max-width: 80%;
}

.quoteWrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 80px 0;
    gap: 20px;
}

.quoteAuthor {
    text-align: right;
    padding-right: 80px;
}

.megagrid {
    display: block;
    position: relative;
    overflow: hidden;
    max-width: 100vw;
    height: 150%;
}
.megagrid img {
    position: relative;
    max-width: 100%;
    z-index: 2;
}
.video {
    position: absolute;
    top: 35.5%;
    left: 22.5%;
    height: 20%;
    width: 14.6%;
    z-index: 3;
}

.grayRect {
    position: absolute;
    background-color: rgb(225, 222, 222);
    height: 80%;
    width: 70%;
    right: 0;
    top: 60%;
    z-index: 1;
}

.logosGrid {
    display: grid;
    gap: 16px;
    grid-template-columns: repeat(4, 1fr);
}

.logosGrid img {
    max-width: 100%;
    cursor: pointer;
}

.grid2 {
    display: grid;
    gap: 16px;
    grid-template-columns: 2fr 3fr;
    align-items: center;
}
.grid5{
    display: grid;
    gap: 16px;
    grid-template-columns: repeat(5, 1fr);
    margin-bottom: 16px;
}
.gridcustom {
    display: grid;
    gap: 16px;
    margin-bottom: 16px;

}
.row1 {
    grid-template-columns: 3fr 3fr 4fr;
}
.row2 {
    grid-template-columns: 1fr 1fr 1fr 2fr;
}
.grid2 img,
.grid5 img,
.gridcustom img {
    max-width: 100%;
    cursor: pointer;
}


.imageslist {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 90px;
    padding: 0;
    margin: 0;
    counter-reset: mycounter;
}

.imageslist li {
    vertical-align: bottom;
    display: flex;
    align-items: flex-start;
}
.imageslist li img {
    max-width: 100%;
    height: auto;
    border: 1px solid black;
}
.imageslist li::before {
    counter-increment: mycounter;
    content: counter(mycounter)". ";
    margin-right: 10px;
    font-size: 16px;
    font-weight: bold;
}
